import React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import { rhythm } from "../utils/typography"

import {talks, writings} from "../../content/more.js"

class BlogIndex extends React.Component {
  render() {
    const { data } = this.props
    const siteTitle = data.site.siteMetadata.title

    return (
      <Layout location={this.props.location} title={siteTitle}>
        <SEO title="More content by me" />
        <h1>Talks</h1>
        {talks.map(({ title, url, date, conf }) => {
          return (
            <div>
              <h3
                style={{
                  marginBottom: rhythm(1 / 4),
                }}
              >
                <a style={{ boxShadow: `none` }} target="_blank" rel="noopener noreferrer" href={url}>
                  {title}
                </a>
              </h3>
              <small>{date} • {conf}</small>
            </div>
          )
        })}

        <h1>Articles</h1>
        {writings.map(({ title, url, date, committent }) => {
          return (
            <div>
              <h3
                style={{
                  marginBottom: rhythm(1 / 4),
                }}
              >
                <a style={{ boxShadow: `none` }} target="_blank" rel="noopener noreferrer" href={url}>
                  {title}
                </a>
              </h3>
              <small>{date} • {committent}</small>
            </div>
          )
        })}
      </Layout>
    )
  }
}

export default BlogIndex



export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      filter: {frontmatter: {draft: {eq: false}}}
      sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            description
          }
        }
      }
    }
  }
`
